.container {
	.grid-wrapper {
		display: grid;
		width: 100%;
		margin: 80px 0 0 80px;
		padding: 18px;
		// margin-left: 5%;
		grid-template-columns: repeat(3, 1fr);
		grid-template-rows: repeat(3, 400px);
		height: 1000px;
		.box1 {
			padding: 8px;
		}
		.box2 {
		margin-left: 10px;
		padding: 8px;


		}
		.box3 {
			padding: 8px;

			margin-left: 10px;
		}
		.news {
			width: 100%;
			margin-bottom: 30px;
			.iconRow {
				margin-bottom: 20px;
     			margin-left: 20px;
      		margin-top: 20px;
				width: 220px;
      		height: 15px;
      		display: flex;
      		font-family: monseratR;
      		font-size: 14px;
      		color: dimgray;
				justify-content: space-between;
				.iconWrap {
					width: 14px;
					height: 13px;
					color: #9d9d9d;
				}

			}
		article {
			padding: 2%;
			h1 {
				font-family: montserrat;
				font-size: 26px;
				font-weight: bold;
				font-stretch: normal;
				font-style: normal;
				line-height: 1.15;
				letter-spacing: -0.52px;
				text-align: left;
				color: #313131;

			}
			p  
			{
				margin-top: 20px;
				margin-bottom:20px ;
				font-family: OpenSans;
  				font-size: 14px;
  				font-weight: 600;
  				font-stretch: normal;
  				font-style: normal;
  				line-height: 1.57;
  				letter-spacing: -0.28px;
  				text-align: left;
  				color: #626262;
			}
		}
		
	}
	}
}
.pic {
	width:100%;
	position: relative;
	min-height: 150px;
	overflow: hidden;
	display: flex;
	 align-items: center;
	 justify-content: center;

	
	img {

		width: 100%;
		display: block;

	}
}
.news#one {
	border-bottom: 5px solid  #2eb398;	
}
.news#two {
	border-bottom: 5px solid #e76049;
}
.news#three {
	border-bottom: 5px solid darkblue;
}

.news#five {
	border-bottom: 5px solid #90899c;
}
.news#six {
	border-bottom: 5px solid  #14b9d6;
}
.news .pic .hover { 
	 position: absolute;
	 
	 left: 0;
	 right: 0;
	 top: 0;
	 z-index: 333;
	 width: 100%;
	 height: 100%;
	background: rgba(255, 68, 0, 0);
	transition: .5s;
	display: flex;
	align-items: center;;
	

	
	a {
		display: block;
		width: 300px ;
		opacity: 0;
		z-index: 333;
		margin: 0 auto;
	transition: .5s;
		
			  
	

		
	}
}
.news .pic:hover .hover a {
	opacity: 1;
}
.news .pic:hover .hover {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 555;
	width: 100%;
	height: 100%;
	display: flex;
  background: #e76049;
  opacity: 0.9;
}
